import React, { useState, useEffect } from 'react';
import * as subscriptions from './graphql/subscriptions';
import { generateClient } from 'aws-amplify/api';
import { Amplify } from 'aws-amplify';
import './App.css'; // Import the CSS file for styling
import { Line } from 'react-chartjs-2';

Amplify.configure({
  API: {
    GraphQL: {
      endpoint: 'https://7mjyaegozvbbxkfiy3gbdmumrq.appsync-api.us-east-1.amazonaws.com/graphql',
      region: 'us-east-1',
      defaultAuthMode: 'apiKey',
      apiKey: 'da2-cjde5iya75donllhph2s3gpvmm',
    }
  }
});

const client = generateClient();

function App() {
  const [subscriptionInput, setSubscriptionInput] = useState('');
  const [subscriptionData, setSubscriptionData] = useState(null);
  const [subscriptionType, setSubscriptionType] = useState('Stock');
  const [subscriptionObject, setSubscriptionObject] = useState(null);
  const [chartData, setChartData] = useState({
    labels: [],     // Timestamps
    datasets: [
      {
        label: 'Price',
        data: [],     // Prices
        fill: false,
        borderColor: 'rgba(75,192,192,1)',
      },
    ],
  });

  const renderSubscriptionTemplate = (subscriptionData, selectedType) => {
    switch (selectedType) {
      case 'Stock':
        return (
          <div>
            <h2>{subscriptionData.shortName}</h2>
            <h3>Price: {subscriptionData.p}</h3>
            <p>Last Updated: {formatTimestamp(subscriptionData.t)}</p>
          </div>
        );
      case 'Crypto':
        return (
          <div>
            <h2>Price: {subscriptionData.price}</h2>
            <p>Volume: {subscriptionData.v}</p>
            <p>Last Updated: {formatTimestamp(subscriptionData.t)}</p>
          </div>
        );
      case 'Forex':
        return (
          <div>
            <h4>Open: {subscriptionData.high}</h4>
            <h4>Close: {subscriptionData.close}</h4>
            <h4>High: {subscriptionData.high}</h4>
            <h4>Low: {subscriptionData.low}</h4>
            <p>Last Updated: {formatTimestamp(subscriptionData.et)}</p>
          </div>
        );
    }
  };

  const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp);
    return date.toLocaleString();
  };

  const handleSubscription = () => {
    if (subscriptionObject) {
      // Unsubscribe and handle the cleanup in the callback
      console.log("got call for unsubscribe")
      subscriptionObject.unsubscribe();
      setSubscriptionObject(null);
      setSubscriptionData(null);
      // Reset chart data
      // setChartData({
      //   labels: [],
      //   datasets: [
      //     {
      //       label: 'Price',
      //       data: [],
      //       fill: false,
      //       borderColor: 'rgba(75,192,192,1)',
      //     },
      //   ],
      // });
    } else {
      console.log(`${subscriptionType}_${subscriptionInput}`);
      const subscription = client.graphql({
        query: subscriptions.subscribe,
        variables: { name: `${subscriptionType}_${subscriptionInput}` }
      }).subscribe({
        next: ({ data }) => {
          const parsedData = JSON.parse(data['subscribe']['data']);
          console.log(parsedData);
          setSubscriptionData(parsedData);
  
          // Update chart data
          // setChartData((prevData) => ({
          //   labels: [...prevData.labels, formatTimestamp(parsedData.t)],
          //   datasets: [
          //     {
          //       ...prevData.datasets[0],
          //       data: [...prevData.datasets[0].data, parsedData.price],
          //     },
          //   ],
          // }));
        },
        error: (error) => console.warn(error),
        complete: () => {
          console.log('Subscription complete.'); // Log when the subscription is complete
        }
      });
  
      setSubscriptionObject(subscription);
    }
  };
  

  useEffect(() => {
    // let chartInstance = null;

    return () => {
      if (subscriptionObject) {
        subscriptionObject.unsubscribe();
        // if (chartInstance) {
        //   chartInstance.destroy();
        // }
        // setChartData({
        //   labels: [],
        //   datasets: [
        //     {
        //       label: 'Price',
        //       data: [],
        //       fill: false,
        //       borderColor: 'rgba(75,192,192,1)',
        //     },
        //   ],
        // });
      }
    };
  }, [subscriptionObject]);

  const isSubscribed = !!subscriptionObject; // Check if subscribed

  return (
    <div className="App">
      <header className="App-header">
        <div className="centered-container">
          <input
            type="text"
            value={subscriptionInput}
            onChange={(e) => setSubscriptionInput(e.target.value)}
            placeholder="Enter subscription name"
          />
          <div className="radio-buttons">
            <label>
              <input
                type="radio"
                value="Stock"
                checked={subscriptionType === 'Stock'}
                onChange={() => setSubscriptionType('Stock')}
                disabled={isSubscribed}
              />
              Stock
            </label>
            <label>
              <input
                type="radio"
                value="Crypto"
                checked={subscriptionType === 'Crypto'}
                onChange={() => setSubscriptionType('Crypto')}
                disabled={isSubscribed}
              />
              Crypto
            </label>
            <label>
              <input
                type="radio"
                value="Forex"
                checked={subscriptionType === 'Forex'}
                onChange={() => setSubscriptionType('Forex')}
                disabled={isSubscribed}
              />
              Forex
            </label>
          </div>
          <button onClick={handleSubscription}>
            {isSubscribed ? 'Unsubscribe' : 'Subscribe'}
          </button>
        </div>
        {subscriptionData && (
          <div className="subscription-box centered-box">
            {renderSubscriptionTemplate(subscriptionData, subscriptionType)}
            {/* {chartData.labels.length > 0 && <Line data={chartData} />} */}
          </div>
        )}
      </header>
    </div>
  );
}

export default App;
